@import '../../../ng-app/src/styles/variables';

/* jura-300 - vietnamese_latin-ext_latin */
@font-face {
    font-family: @font-jura;
    font-style: normal;
    font-weight: 300;
    src: url('/Content/fonts/Jura/jura-v24-vietnamese_latin-ext_latin-300.eot'); /* IE9 Compat Modes */
    src: local(''), url('/Content/fonts/Jura/jura-v24-vietnamese_latin-ext_latin-300.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('/Content/fonts/Jura/jura-v24-vietnamese_latin-ext_latin-300.woff2') format('woff2'), /* Super Modern Browsers */
    url('/Content/fonts/Jura/jura-v24-vietnamese_latin-ext_latin-300.woff') format('woff'), /* Modern Browsers */
    url('/Content/fonts/Jura/jura-v24-vietnamese_latin-ext_latin-300.ttf') format('truetype'), /* Safari, Android, iOS */
    url('/Content/fonts/Jura/jura-v24-vietnamese_latin-ext_latin-300.svg#Jura') format('svg'); /* Legacy iOS */
}
