@import '../../../ng-app/src/styles/variables';

/* montserrat-100 - latin */
@font-face {
    font-family: @font-montserrat;
    font-style: normal;
    font-weight: 100;
    src: local('Montserrat Thin'), local('Montserrat-Thin'), url('/Content/fonts/Montserrat/montserrat-v14-latin-100.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
    url('/Content/fonts/Montserrat/montserrat-v14-latin-100.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
/* montserrat-100italic - latin */
@font-face {
    font-family: @font-montserrat;
    font-style: italic;
    font-weight: 100;
