//if enhanced images are used, img built-in rendering is positioned out of
//view of the rendering box by moving it the width and height of the acutal
//window to ensure it won't be visible and let CSS background take over.
img[enhanced] {
    object-fit: none;
    object-position: -100vw -100vh;
}

//needed styles from bootstrap/angularjs to keep Angular site looking correct
html {
    position: relative;

    -webkit-text-size-adjust: 100%;
    text-size-adjust: 100%;

    body {
        margin: 0px;
        line-height: 1.3;

        font-size: 10.5pt;
    }
}

img {
    vertical-align: middle;
}

figure {
    margin: 0px;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

.hide {
    display: none !important;
}

.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0px;
    overflow: hidden;
    clip: rect(0 0 0 0);
    border: 0px;
}

.hidden {
    display: none !important;
    visibility: hidden !important;
}

  .right-caption {
    display: flex;
    align-items: center;

    @media @breakpoint-lt-md {
        height: 14px;
        width: 14px;
    }
}

.clickable {
    cursor: pointer;
}

.disabled,
a[disabled] {
    cursor: not-allowed;
    opacity: 0.5;
    pointer-events: none;
}

p {
    margin: 0px 0px 10px;
}

// Catchall baseclass
.glyphicon {
    position: relative;
    top: 1px;
    display: inline-block !important;
    font-family: 'Glyphicons Halflings' !important;
    font-style: normal;
    font-weight: normal;
    line-height: 1 !important;
    -webkit-font-smoothing: antialiased !important;
}

